:root {
    --background: 0 0% 7%;
    --foreground: 0 0% 98%;

    --card: 0 0% 10%;
    --card-foreground: 0 0% 98%;

    --popover: 0 0% 8%;
    --popover-foreground: 0 0% 98%;

    --primary: 15 85% 55%;
    --primary-foreground: 0 0% 98%;

    --secondary: 0 0% 15%;
    --secondary-foreground: 0 0% 98%;

    --muted: 0 0% 20%;
    --muted-foreground: 0 0% 60%;

    --accent: 15 85% 55%;
    --accent-foreground: 0 0% 98%;
    --accent-light: 15 85% 65%;
    --accent-dark: 15 85% 45%;
    --accent-glow: 15 85% 55%;

    --destructive: 0 84.2% 60.2%;
    --destructive-foreground: 0 0% 98%;

    --border: 0 0% 15%;
    --input: 0 0% 15%;
    --ring: 15 85% 55%;

    --radius: 0.75rem;

    /* Surface Colors for Depth */
    --surface-50: 0 0% 5%;
    --surface-100: 0 0% 8%;
    --surface-200: 0 0% 12%;
    --surface-300: 0 0% 16%;
    --surface-400: 0 0% 20%;
    --surface-500: 0 0% 24%;

    --hero-glow: 15 100% 65%;
    --transition-smooth: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
    --transition-spring: all 0.5s cubic-bezier(0.175, 0.885, 0.32, 1.275);

    /* Premium Gradient Presets */
    --gradient-primary: linear-gradient(135deg, hsl(var(--accent)) 0%, hsl(var(--primary)) 100%);
    --gradient-premium: linear-gradient(135deg, hsl(var(--accent)) 0%, #a855f7 50%, #ec4899 100%);
    --gradient-accent-purple: linear-gradient(135deg, hsl(var(--accent)) 0%, #8b5cf6 100%);
    --gradient-shine: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.3), transparent);
    --gradient-mesh: radial-gradient(at 40% 20%, hsl(var(--accent) / 0.3) 0px, transparent 50%),
      radial-gradient(at 80% 0%, hsl(280 100% 70% / 0.2) 0px, transparent 50%),
      radial-gradient(at 0% 50%, hsl(200 100% 70% / 0.2) 0px, transparent 50%);

    /* Glass Morphism */
    --glass-bg: rgba(255, 255, 255, 0.05);
    --glass-bg-strong: rgba(255, 255, 255, 0.08);
    --glass-border: rgba(255, 255, 255, 0.1);
    --glass-border-strong: rgba(255, 255, 255, 0.15);
    --glass-blur: 20px;
    --glass-blur-strong: 40px;

    /* Glow Effects */
    --glow-sm: 0 0 20px hsl(var(--accent) / 0.3);
    --glow-md: 0 0 40px hsl(var(--accent) / 0.4);
    --glow-lg: 0 0 60px hsl(var(--accent) / 0.5);
    --glow-xl: 0 0 80px hsl(var(--accent) / 0.6);

    /* Shadow System */
    --shadow-premium: 0 20px 60px -15px rgba(0, 0, 0, 0.5), 0 0 0 1px hsl(var(--accent) / 0.1);
    --shadow-glow: 0 8px 32px hsl(var(--accent) / 0.3);
    --shadow-lift: 0 24px 48px rgba(0, 0, 0, 0.4);
  }
*{
  border-color: hsl(var(--border));
}
html {
    scroll-behavior: smooth;
    scroll-padding-top: 80px;
    /* Account for fixed header */
  }
@media (prefers-reduced-motion: reduce) {
    html {
      scroll-behavior: auto;
    }

    * {
      animation-duration: 0.01ms !important;
      animation-iteration-count: 1 !important;
      transition-duration: 0.01ms !important;
    }
  }
body{
  background-color: hsl(var(--background));
  color: hsl(var(--foreground));
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
    font-family: 'Space Grotesk', system-ui, -apple-system, sans-serif;
    overflow-x: hidden;
    min-height: 100vh;
    font-size: 17px;
    line-height: 1.7;
}
/* Enhanced Text Selection */
::-moz-selection {
    background: hsl(var(--accent) / 0.3);
    color: hsl(var(--foreground));
  }
::selection {
    background: hsl(var(--accent) / 0.3);
    color: hsl(var(--foreground));
  }
::-moz-selection {
    background: hsl(var(--accent) / 0.3);
    color: hsl(var(--foreground));
  }
/* Enhanced Focus Styles for Better Accessibility */
*:focus-visible {
    outline: 2px solid hsl(var(--accent));
    outline-offset: 2px;
  }
/* Fluid Typography */
h1{
  font-size: clamp(2.5rem, 5vw + 1rem, 6rem);
  font-weight: 700;
  line-height: 1.25;
  letter-spacing: -0.025em;
}
h2{
  font-size: clamp(2rem, 4vw + 1rem, 4.5rem);
  font-weight: 700;
  line-height: 1.25;
  letter-spacing: -0.025em;
}
h3{
  font-size: clamp(1.5rem, 3vw + 1rem, 3rem);
  font-weight: 600;
  line-height: 1.375;
  letter-spacing: -0.025em;
}
p{
  font-size: clamp(1rem, 1.5vw + 0.5rem, 1.25rem);
  line-height: 1.625;
}
/* Premium Custom Scrollbar */
::-webkit-scrollbar {
    width: 10px;
  }
::-webkit-scrollbar-track {
    background: hsl(var(--background));
    border-left: 1px solid hsl(var(--border));
  }
::-webkit-scrollbar-thumb {
    background: linear-gradient(180deg, hsl(var(--accent)), hsl(270 85% 55%));
    border-radius: 10px;
    border: 2px solid hsl(var(--background));
  }
::-webkit-scrollbar-thumb:hover {
    background: linear-gradient(180deg, hsl(var(--accent) / 0.9), hsl(270 85% 55% / 0.9));
    box-shadow: 0 0 10px hsl(var(--accent) / 0.5);
  }
::-webkit-scrollbar-thumb:active {
    background: linear-gradient(180deg, hsl(var(--accent)), hsl(270 85% 55%));
  }
/* Firefox Scrollbar */
* {
    scrollbar-width: thin;
    scrollbar-color: hsl(var(--accent)) hsl(var(--background));
  }
